@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Importing fonts */

/* Defining general styles and custom properties */
:root {
  /* Font Colors */
  --light-font-color: rgb(87, 46, 229);
  --dark-font-color: rgb(255, 255, 255);
  --font-color: rgb(87, 46, 229);
  /* Background Colors */
  --light-bkgd-color: rgb(255, 255, 255);
  --dark-bkgd-color: rgb(55, 55, 55);
  --bkgd-color: rgb(255, 255, 255);
  /* Utils */
  --search-btn-color: rgb(87, 46, 229);
  --border-radius: 1.5rem;
}

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  font-family: "Roboto", sans-serif;
}

body {
  border-top: 0.375rem solid rgba(104, 9, 225, 1);
  background-color: rgb(255, 255, 255);
  background-color: var(--bkgd-color);
  transition: background-color 1s ease;
}

button:active{
  -webkit-transform: scale(0.98);
          transform: scale(0.98);
}

.Header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100vw;
    height: 2rem;
    padding: 2rem 1.5rem;    
  }

  .Header__Logo{
    width: 2rem;
    transition: all 0.5s ease;
  }
  
  .Header__ThemeButton {
    border-radius: var(--border-radius);
    border: 1px solid var(--font-color);
    font-size: 0.5rem;
    padding: 0.5rem 1.25rem;
    font-weight: bold;
    background-color: transparent;
    color: var(--font-color);
    transition: all 0.5s ease;
  }

  .Header__ThemeButton:hover{
    border: 1px solid var(--font-color);
    color: var(--bkgd-color);
    background-color: var(--font-color);
  }

  @media screen and (min-width: 600px){
    .Header {
      height: 2.5rem;
      padding: 2.5rem 2.8rem;
    }

    .Header__Logo{
      width: 2.5rem;
    } 

    .Header__ThemeButton {
      font-size: 0.5rem;
      padding: 0.5rem 1.25rem;
    }
  }

  @media screen and (min-width: 800px){
    .Header{
      height: 2.75rem;
      padding: 2.75rem 3.5rem;
    }

    .Header__Logo{
      width: 3.25rem;
    }

    .Header__ThemeButton { 
      font-size: 0.75rem;
      padding: 0.75rem 1.75rem;
    }
  }

  @media screen and (min-width: 1000px){
    .Header{
      height: 3.13rem;
      padding: 3rem 4.69rem;
    }

    .Header__Logo{
      width: 3.75rem;
    }

    .Header__ThemeButton { 
      font-size: 0.875rem;
      padding: 1rem 2.25rem;
    }
  }
.Searchbar {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 0 1.11rem;
    width: 100vw;
  }
  
  .Searchbar__Welcome {
    margin-top: 1.11rem;
    font-size: 1rem;
    color: var(--font-color);
    font-weight: 400;
    transition: all 0.5s ease;
  }
  
  .Searchbar__Welcome span {
    font-weight: bold;
  }
  
  .Searchbar__Image {
    margin-top: 1rem;
    width: 32vw;
    max-width: 30rem;
  }
  
  .Searchbar__Wrapper {
    display: flex;
    position:relative;
    flex-direction: column;
    border: 1px solid var(--font-color);
    border-radius: var(--border-radius);
    width: 60vw;
    max-width: 40rem;
    box-shadow: 0 2px 5px 1px rgba(64,60,67,.30);
    transition: color 0.5s ease;
  }

  .Searchbar__Wrapper-With-Suggestions{
    border-radius: var(--border-radius) var(--border-radius) 0 0;
    border-bottom: none;
  }

  .Searchbar__SuggestionsWrapper{
    display: flex;
    position: relative;
    margin: 0 -1px;
    z-index: 1;
  }

  .Searchbar__Suggestions{
    display: flex;
    flex-direction: column;
    position: absolute;
    background-color: var(--bkgd-color);
    width: 100%;
    border-radius: 0 0 var(--border-radius) var(--border-radius);
    border-right: 1px solid var(--font-color);
    border-left: 1px solid var(--font-color);
    border-bottom: 1px solid var(--font-color);
    border-top: none;
    box-shadow: 0 2px 5px 1px rgba(64,60,67,.30);
    -webkit-clip-path: inset(0 -10px -10px -10px);
            clip-path: inset(0 -10px -10px -10px);
  }

  .Searchbar__Wrapper:hover{
    box-shadow: 0 2px 8px 1px rgba(64,60,67,.55);
  }

  .Searchbar__Query{
    display: flex;
    align-items: stretch;
    border-radius: var(--border-radius);
  }
  
  .Searchbar__Input {
    border-radius: var(--border-radius) 0 0 var(--border-radius);
    width: 84%;
    padding-left: 1rem;
    height: 2rem;
    border: none;
    color: var(--font-color);
    font-size: .875rem;
    font-weight: 100;
    background-color: transparent;
    transition: all 0.5s ease;
  }

  .Searchbar__Icon{
    background: url(/reactgifossprint/static/media/suggestion_icon.e6bec98a.svg);
    background-size: var(--filter-icon-bkgd-size-sm);
    background-repeat: no-repeat;
    background-position-x: 1rem;
    background-position-y: center;  
    padding-left: 3rem;
    cursor: pointer;
  }
  
  ::-webkit-input-placeholder {
    color: var(--font-color);
    -webkit-transition: color 0.5s ease;
    transition: color 0.5s ease;
  }
  
  :-ms-input-placeholder {
    color: var(--font-color);
    -ms-transition: color 0.5s ease;
    transition: color 0.5s ease;
  }
  
  ::placeholder {
    color: var(--font-color);
    transition: color 0.5s ease;
  }
  
  :-ms-input-placeholder {
    /* Internet Explorer 10-11 */
    color: var(--font-color);
    -ms-transition: color 0.5s ease;
    transition: color 0.5s ease;
  }
  
  ::-ms-input-placeholder {
    /* Microsoft Edge */
    color: var(--font-color);
    -ms-transition: color 0.5s ease;
    transition: color 0.5s ease;
  }
  
  .Searchbar__Btn {
    border-radius: 0 var(--border-radius) var(--border-radius) 0;
    width: 16%;
    background-color: var(--search-btn-color);
    border: none;
    transition: border 0.5s ease;
    cursor: pointer;
  }

  .Searchbar svg{
    height: 10px;
  }

  .Searchbar__CloseSuggestions{ 
    border-radius: 0 var(--border-radius) var(--border-radius) 0;
    width: 16%;
    background: transparent;
    border: none;
    transition: border 0.5s ease;
    cursor: pointer;
  }

  .Searchbar__CloseSuggestions:hover{
    background-color: var(--font-color); 
  }

  .Searchbar__CloseSuggestions svg{
    fill:var(--font-color);
  }

  .Searchbar__CloseSuggestions:hover svg{
    fill:var(--bkgd-color);
  }

  .Searchbar__SuggestionItem{
    padding-left: 2rem;
    color: var(--font-color);
    font-size: .875rem;
    font-weight: 100;
    margin: .25rem;
    background-color: transparent;
    cursor: pointer;
  }

  .Searchbar__SuggestionItem:hover{
    background-color: var(--font-color);
    color: var(--bkgd-color);
  }

  .Searchbar__SuggestionItem:last-of-type{
    border-radius: 0 0 var(--border-radius) var(--border-radius);
    padding-bottom: .5rem;
  }
  
  .Searchbar__SuggestionItem:first-of-type{
    padding-top: .5rem;
    padding-bottom: .1rem;
    margin-top: 0; 
    border-top: 1px solid rgba(211, 211, 211, 0.5);
  }

  .Searchbar__SuggestionItem::before{
    content: url(/reactgifossprint/static/media/suggestion_icon.e6bec98a.svg);
    margin: 0 1rem 0 -1rem;
  }

  /* Media Queries */
  @media screen and (min-width: 600px){
    .Searchbar {
      padding: 0 2.2rem;
    }
    
    .Searchbar__Welcome {
      margin-top: 2.2rem;
      font-size: 1.13rem;
    }

    .Searchbar__Input, .Searchbar__SuggestionItem{
      font-size: 1rem;
    }

    .Searchbar svg{
      height: 15px;
    }

  }

  @media screen and (min-width: 800px){
    .Searchbar {
      padding: 0 3.29rem;
    }

    .Searchbar__Welcome {
      margin-top: 3.29rem;
      font-size: 1.69rem;
    }

    .Searchbar__Input {
      padding-left: 1.5rem;
      height: 2.4rem;
      font-size: 1.125rem;
    }

    .Searchbar__SuggestionItem{
      font-size: 1.125rem;
    }

    .Searchbar__Icon{
      padding-left: 3rem;
    }

    .Searchbar svg{
      height: 20px;
    }

  }

  @media screen and (min-width: 1000px){
    .Searchbar {
      padding: 0 4.375rem;
    }

    .Searchbar__Welcome {
      margin-top: 4.375rem;
      font-size: 2.25rem;
    }

    .Searchbar__Input {
      padding-left: 2rem;
      height: 3.25rem;
    }

    .Searchbar__Icon{
      padding-left: 3rem;
    }
  }
.Results {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100vw;
    padding: 1.75rem;
}

.Results__Header{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  width: 100%;
  margin: .3rem 0;
}  

.Results__Title{
  margin: .5rem 0;
  font-size: .75rem;
  font-weight: 400;
  color: var(--font-color);
  transition: all 0.5s ease;
}

.Results__Quantity{
  padding: 0.3rem;
  border: 1px solid rgb(211, 211, 211);
  border-radius: 0.5rem;
  margin-bottom: 0.5rem;
}

.Results__QuantityLabel{
  font-size: .75rem;
  color: var(--font-color);
  margin-right: 0.5rem;
}

.Results__QuantityOptions{
  font-size: .75rem;
  color: var(--search-btn-color) !important;
  border-radius: 6px;
}

.Results__InputQuery{
  background-color: lightgray;
  border-radius: 8px;
  font-size: .75rem;
  padding: .1rem .3rem;
  color: var(--search-btn-color);
  transition: all 0.5s ease;
}

.Results__Grid {
  /* Prevent vertical gaps */
  line-height: 0;
  -webkit-column-gap: 0;
  -moz-column-gap: 0;
  grid-column-gap: 16px;
  -webkit-column-gap: 16px;
          column-gap: 16px;
  -webkit-column-count: 1;
  column-count: 1;  
}

.Results__Grid-BorderTop{
  border-top: 1px solid rgba(211, 211, 211, .5);
}

.Results__Card {
  /* Just in case there are inline attributes */
  width: 100% !important;
  height: auto !important;
  border-radius: 8px;
  margin: 8px;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.15), 0 2px 2px rgba(0, 0, 0, 0.15),
    0 4px 4px rgba(0, 0, 0, 0.15), 0 8px 8px rgba(0, 0, 0, 0.15);
}

.Results__Error, .Results__Welcome{
  display: flex;
  width: 75%;
  align-items: center;
  justify-content: center;
}

.Results__Error .Results__Card{
  max-width: 4rem;
}

.Results__Welcome .Results__Card{
  max-width: 6rem;
}

.Results__Error .Results__ErrorText, 
.Results__Welcome .Results__WelcomeText, 
.Results__QuantityOptions,
.Results__QuantityLabel, 
#loader{
  font-size: .75rem;
  color: var(--font-color);
  align-self: center;
  transition: all 0.5s ease;
}

.Results__Loader {
  display: inline-block;
  width: 80px;
  height: 80px;
}

.Results__Loader:after {
  content: "";
  display: block;
  width: 64px;
  height: 64px;
  margin: 8px;
  border-radius: 50%;
  border: 6px solid var(--font-color);
  border-color: var(--font-color) transparent var(--font-color) transparent;
  -webkit-animation: loader-animation 1.3s linear infinite;
          animation: loader-animation 1.3s linear infinite;
}

/* Loader animation */
@-webkit-keyframes loader-animation {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes loader-animation {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

/* Media Queries */
@media screen and (min-width: 480px){
  .Results__Header{ 
    flex-direction: row;
    align-items: baseline;
    justify-content: space-between;
  }

  .Results__Title, .Results__InputQuery{
    font-size: 1rem;
  }

  .Results__InputQuery{
    padding: .2rem .3rem;
  }

  .Results__Grid {
    -webkit-column-count: 2;
    column-count: 2;
  }  
}

@media screen and (min-width: 600px){

  .Results__Title, .Results__InputQuery{
    font-size: 1.13rem;
  }

  .Results__Grid {
    -webkit-column-count: 3;
    column-count: 3;
  } 

  .Results__Error .Results__ErrorText, 
  .Results__Welcome .Results__WelcomeText,
  .Results__QuantityOptions,
  .Results__QuantityLabel, 
  #loader{
    font-size: 0.875rem;
  }
}

@media screen and (min-width: 800px){
  .Results__Header{
    margin: .5rem 0;
  }  
  
  .Results__InputQuery{
    padding: .3rem .5rem;
  }

  .Results__Grid {
    -webkit-column-count: 4;
    column-count: 4;
  }

  .Results__Error .Results__ErrorText, 
  .Results__Welcome .Results__WelcomeText, 
  .Results__QuantityOptions,
  .Results__QuantityLabel,
  #loader{
    font-size: 1rem;
  }

}

@media screen and (min-width: 1000px){
  .Results__Header{
    margin: .75rem 0;
  } 
  
  .Results__Grid {
    -webkit-column-count: 5;
    column-count: 5;
  }
}

@media screen and (min-width: 1200px){  
  .Results__Header{
    margin: 1rem 0;
  } 

  .Results__Grid{
    -webkit-column-count: 6;
    column-count: 6;
  }

  .Results__Error .Results__ErrorText, 
  .Results__Welcome .Results__WelcomeText,
  .Results__QuantityOptions, 
  .Results__QuantityLabel,
  #loader{
    font-size: 1.3rem;
  }
}
